.header{
    text-align: center;
    width: calc(100% - 160px);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    color: #333;
    font-size: 18px;
    height: 60px;
}

.header h2{
    margin: 0;
}

@media(max-width: 800px){
    .header h2{
        font-size: 20px;
    }
}

@media(max-width: 600px){
    .header{
        width: calc(100% - 130px);
    }
    .header h2{
        font-size: 16px;
    }
}

@media(max-width: 550px){
    .header h2{
        font-size: 14px;
    }
}